.about {
  margin: 160px auto 0;
  display: flex;
  justify-content: center;
}

.about__side-text-container {
  align-self: end;
  width: 19%;
  margin: auto 35px 35px 0;
}

.about__side-text-title {
  font: var(--font-h-l);
  font-weight: 700;
  margin: 0;
}
.about__side-text-subtitle {
  font: var(--font-m);
}

.about__text-container {
  width: 62%;
  margin: 0 0 35px 35px;
}

.about__title {
  margin: 0;

  font: var(--font-m);
  font-weight: 700;
  color: var(--color-l-grey);
  letter-spacing: 0.22rem;
  line-height: 1.2;
  text-transform: uppercase;
  opacity: 0.5;
  white-space: nowrap;
}

.about__text {
  font: var(--font-ml);
  font-size: 1.625rem;
  margin: 0;
}
