.rates {
  margin: 80px auto;
  padding: 0 46px;
        border-bottom: 1px solid var(--color-l-grey);
}

.rates__rates-container {
    display: flex;
    justify-content: space-between;
    margin: 0 0 0 40%;
}

.rates__rate {
    margin: auto;
    font: var(--font-h-s);
    padding: 20px 20px;
}

.rates__option-container {
            display: flex;
    justify-content: space-between;
        border-top: 1px solid var(--color-l-grey);
}

.rates__option-title {
    width: 40%;
        margin: auto auto auto 0;
        padding: 20px 0;
    font: var(--font-m);
}

.rates__option-position {
        width: 15%;
        text-align: center;
    display: flex;
}

.rates__option-pic {
        margin: auto;
}
