.text {
  margin: 0;
  font: var(--font-ml);
  line-height: 1.6;
  color: var(--color-dark-grey);
}

.text h3 {
  margin: 40px 0 0;
      font: var(--font-h-m);
      line-height: 1.3;
    color: var(--color-black);
    letter-spacing: .03rem;
    text-transform: uppercase;
}

.text p {
  margin: 16px 0 0;
}

.text strong {
  font-weight: 500 ;
}

.text ul {
  margin: .5rem 0 0;
  padding-left: 1rem;
}

.text li::marker {
  color: var(--color-accent);
  margin: 0;
}

.text ol {
  margin: .5rem 0 0;
  padding-left: 1.56rem;
}

.text ol li::marker {
  content: counter(list-item) ".  ";
}

.text blockquote {
  margin: 1rem 0 0;
  padding: 0 0 0 2rem;
  border-left: solid 2px var(--color-accent);
  font-size: 1.625rem;
  color: var(--color-black);
  line-height: 1.4;
}
